<template>
  <div class="config">
    <div class="header">
      <img alt="Vue logo" width="50" src="./assets/logo.png" />
      <h3>{{ cnt }}</h3>
      <button @click="restart">重开</button>
    </div>

    <div>颜色数量</div>
    <input type="number" v-model="n" @change="change_n" />
    <div class="colors">
      <input v-for="(c, i) in colors" :key="i" v-model="colors[i]" type="color" />
    </div>
  </div>

  <div class="game">
    <MyBottle v-for="(b, i) in bottles" :key="i" :colors="b" @click="pick(i)" :show="float_i != i" />
  </div>

  <div v-show="win" class="winner"></div>

  <MyBottle class="float" :colors="float_bottle" v-show="float_i >= 0" :show="float_i >= 0"
    :style="{ left: left + 'px', top: top + 'px' }" />
</template>

<script>
import MyBottle from "./components/MyBottle.vue";
import { game, randint } from "@/bottle.js";

export default {
  name: "App",
  data() {
    return {
      bottles: [],
      float_i: -1,
      left: 0,
      top: 0,
      cnt: 0,
      colors: [],
      n: 0,
      win: false,
    };
  },
  computed: {
    float_bottle() {
      return this.float_i >= 0 ? this.bottles[this.float_i] : [];
    },
  },
  components: {
    MyBottle,
  },
  mounted() {
    this.n = game.colors.length;
    this.colors = game.colors;
    this.restart();
    window.addEventListener("mousemove", (e) => {
      this.top = e.clientY + 10;
      this.left = e.clientX + 10;
    });
    window.addEventListener("touchmove", (e) => {
      e = e.touches[0];
      this.top = e.clientY + 10;
      this.left = e.clientX + 10;
    });
  },
  methods: {
    pick(i) {
      if (this.float_i == -1) this.float_i = i;
      else if (this.float_i == i) this.float_i = -1;
      else {
        game.pour(this.float_i, i);
        this.bottles[i] = game.get_bottle(i);
        this.bottles[this.float_i] = game.get_bottle(this.float_i);
        this.cnt = game.cnt;
        this.win = game.is_win();
      }
    },
    restart() {
      game.colors.length = this.n;
      game.colors = this.colors;
      game.init();
      this.bottles = game.bottles.map((v, i) => game.get_bottle(i));
      this.float_i = -1;
      this.cnt = game.cnt;
      this.win = false;
    },
    change_n() {
      let n = this.colors.length;
      this.colors = new Array(this.n)
        .fill(0)
        .map((v, i) => (i < n ? this.colors[i] : this.get_random_color()));
    },
    get_random_color() {
      let i = 0;
      for (let j = 0; j < 3; j++) {
        i *= 256;
        i += 64 * randint(0, 3);
      }
      i = "000000" + i.toString(16);
      return "#" + i.slice(-6);
    },
  },
};
</script>

