var randint = (begin, end) => {
    return Math.floor(Math.random() * (end - begin)) + begin;
};

var shuffle = (array) => {
    let n = array.length;
    for (let i = 0; i < n; i++) {
        let j = randint(i, n);
        [array[i], array[j]] = [array[j], array[i]];
    }
};


var is_full = (array) => {
    for (let i = 0; i < array.length; i++) {
        if (array[i] < 0) return false;
    }
    return true;
};

var is_empty = (array) => {
    for (let i = 0; i < array.length; i++) {
        if (array[i] >= 0) return false;
    }
    return true;
};

// 拿走最高 i=0 最高 -> i=3最低
var get_top = (array) => {
    for (let i = 0; i < array.length; i++) {
        let v = array[i];
        if (v >= 0) {
            array[i] = -1;
            return v;
        }
    }
};

// 放到最低
var put_bottom = (array, v) => {
    let i;
    for (i = 1; i < array.length; i++) {
        if (array[i] >= 0) break;
    }
    array[i - 1] = v;
};

var all_same = (array) => {
    let v = array[0];
    for (let i = 1; i < array.length; i++) {
        if (array[i] != v) return false;
    }
    return true;
};


var game = {
    colors: [
        "#00c8ff", "#9eeaff", "#6a00ff",
        "#ae3251", "#e79791", "#089896",
        "#402131", "#44ff00", "#035b5c",
        "#fbcd6d", "#364863", "#ff0000"
    ],
    height: 4,
    bottles: [],
    cnt: 0,
    init() {
        this.build();
    },
    build() {
        let n = this.colors.length;
        let h = this.height;

        // n个纯色瓶子混合
        let ds = new Array(n * h).fill(0).map((v, i) => Math.floor(i / h));
        shuffle(ds);
        ds = new Array(n).fill(0).map((v, i) => ds.slice(i * h, (i + 1) * h));

        // 增加两个空瓶子
        let es = new Array(2).fill(0).map(() => new Array(h).fill(-1));

        // 合并
        this.bottles = ds.concat(es);

        this.cnt = 0;
    },
    pour(src, obj) {
        let n = this.bottles.length;

        if (src < 0 || obj < 0 || src >= n || obj >= n || src == obj) return false;
        if (is_full(this.bottles[obj]) || is_empty(this.bottles[src])) return false;

        put_bottom(this.bottles[obj], get_top(this.bottles[src]));
        this.cnt += 1;
        return true;
    },
    get_bottle(i) {
        let n = this.bottles.length;

        if (i < 0 || i >= n) return;
        return this.bottles[i].map((v) => v < 0 ? "" : this.colors[v]);
    },
    is_win() {
        for (let i = 0; i < this.bottles.length; i++) {
            if (!all_same(this.bottles[i])) return false;
        }
        return true;
    },
};

export {
    game,
    randint
};
